<template>
    <div id="select-os-page">
        <!-- Templates CARDS  -->
        <div class="vx-row">
          <div class="ml-4 my-5">
              <h1 class="text-dark">List Of Available OS</h1>
              <p class="text-dark">Select an OS</p>
          </div>
        </div> 
        <div class="vx-row mt-10" v-if="OS">
            <div class="vx-col w-full md:w-1/5 sm:w-1/2 mb-base"  v-for="vm in os" :key="vm.id" @click="$router.push('/select-os/category/'+vm.id).catch(() => {})">
                <vx-card class="text-center cursor-pointer">
                    <img :src="vm.logo" alt="graphic" width="80" height="70" class="mx-auto mb-4">
                    <h4 class="mb-2">{{ vm.name }}</h4>
                </vx-card>
            </div>
        </div>
        <div v-else class="text-center">
            <img src="@/assets/images/logo/xongl_wbi.png" class="img-fluid" alt="wait.." width="100">
            <h4 class="text-center text-primary mt-4">Please wait..</h4>
        </div> 
    </div>
</template>

<script>
import axios from 'axios';
export default{
    data() {
        return {   
           os: {},
           OS: false,
        }
    },
    created(){
                // authenticated or not
        if(localStorage.getItem('token')==null)
        {
        this.$router.replace('/').catch(() => {})  
        } 
    },
    mounted() {

                const tokenurl = 'api/templates';
                axios({
                    method: 'get',
                    url: tokenurl,
                })
                .then((response) => {
                 this.os =  response.data.templates.filter(os =>{
                  return os.description === "OS"
                 });
                  this.OS = true;
                })
                .catch((error) => {
                    this.errors = error;
                })

      },
}
</script>

